* {
  box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
  font-size: 16px;
}

.people {
  --column: 2;
  grid-template-columns: repeat(var(--column), 200px);
  justify-content: center;
  gap: 40px 30px;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    --column: 4;
  }
}

.person {
  padding: 10px;
  transition: transform 1s;
  position: relative;
}

.person:hover {
  transform-origin: top;
  z-index: 1;
  background-color: #eee;
  border-radius: 10px;
  transform: scale(1.4);
}

.person .photo {
  margin-bottom: 8px;
  position: relative;
}

.person .photo img {
  border-radius: 50%;
  width: 100%;
  transition: border-radius 1s;
}

.person .photo .rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-weight: 700;
  line-height: 18.4px;
  display: flex;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.details {
  flex-direction: column;
  align-items: center;
  gap: 8px;
  display: flex;
}

.details .name {
  color: #2f4f4f;
  font-size: 20px;
  font-weight: 700;
  line-height: 23px;
}

.details .extra {
  color: #000;
  font-size: 18px;
  font-weight: 400;
  display: none;
}

.person:hover img {
  border-radius: 60px;
}

.person:hover .extra {
  display: block;
}
